 <template>
  <div class="app-container">
    <div ref="gantt" class="left-container"></div>
    <div class="label">
      <div class="label-item">
        <div class="color blue"></div>
        
      </div>
      <div class="label-item">
        <div class="color green"></div>
        
      </div>
      <div class="label-item">
        <div class="color red"></div>
        
      </div>
    </div>
  </div>
</template>
<script>
  // 
  import gantt from 'dhtmlx-gantt'  
  import 'dhtmlx-gantt/codebase/skins/dhtmlxgantt_terrace.css'
  import './locale_cn'  // 

  export default {
    name: 'gantt',
    data() {
      return {
        tasks: {
          data: []
        }
      }
    },
    methods: {
      //-
      DateDifference (strDateStart, strDateEnd) {
        var begintime_ms = Date.parse(new Date(strDateStart.replace(/-/g, '/'))) //begintime 
        var endtime_ms = Date.parse(new Date(strDateEnd.replace(/-/g, '/')))   // endtime 
        var date3 = endtime_ms - begintime_ms //
        var days = Math.floor(date3 / (24 * 3600 * 1000))
        return days
      },
      initData () {
        this.tasks.data = [
          {
            text: '', // 
            start_date: '19-07-2021', // 
            id: 1, // id
            duration: 4, // start_date
            progress: 0.6,
            type: 1
          },
          {
            text: '', // 
            start_date: '19-07-2021', // 
            id: 2, // id
            duration: 3, // start_date
            type: 2,
            progress: 0.3,
          },
          {
            text: '', // 
            start_date: '18-07-2021', // 
            id: 3, // id
            duration: 5, // start_date
            type: 3
          },
          {
            text: '', // 
            start_date: '20-07-2021', // 
            id: 4, // id
            duration: 6, // start_date
            type: 4,
            progress: 0.1,
          },
        ].map(function(current, ind, arry) {
          var newObj = {}
          if (current.type) { //type  
            if (current.type == 1) { //
              newObj = Object.assign({}, current, { 'color': '#3b70b3' })
            } else if (current.type == 2) { //
              newObj = Object.assign({}, current, { 'color': '#3b70b3' })
            } else if (current.type == 3) { //
              newObj = Object.assign({}, current, { 'color': '#339d66' })
            } else if (current.type == 4){
              newObj = Object.assign({}, current, { 'color': '#cd5252' })
            }
          } else { //
            newObj = Object.assign({}, current, { 'color': '#5692f0' })
          }
          return newObj
        })
      }
    },
    mounted() {
      this.initData()
      
      //, , 
      gantt.config.autosize = false
      //
      gantt.config.readonly = true
      //
      gantt.config.show_grid = true
      //
      gantt.config.columns = [
        { name: 'text', label: '', tree: true, width: '153' }
      ]
      //
      gantt.config.show_task_cells = true

      //x
      gantt.config.date_scale = '' + '%D'

      //tasktask
      gantt.config.fit_tasks = true
      //
      gantt.config.row_height = 47

      // 
      gantt.init(this.$refs.gantt)
      // 
      gantt.parse(this.tasks)
    }
  }
</script>

<style lang="less" scoped>
  /deep/ .gantt_tree_icon {
    display: none;
  }
  /deep/ .gantt_task_content {
    display: none;
  }
  /deep/ .gantt_task_line.gantt_task_inline_color {
    height: 16px !important;
    margin-top: 13px;
    border-radius: 8px !important;
  }
  /deep/ .gantt_grid_data .gantt_cell {
    text-indent: 20px;
  }
  .app-container {
    width: 100%;
    height: 100%;
    /deep/ .left-container {
      width: 100%;
      height: calc(100% - 30px);
    }
  }
  .label {
    height: 30px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-size: 14px;
    .label-item {
      display: flex;
      align-items: center;
      margin-right: 15px;
      .color {
        height: 10px;
        width: 20px;
        margin-right: 8px;
      }
      .blue {
        background: #3b70b3;
      }
      .green {
        background: #339d66;
      }
      .red {
        background: #cd5252;
      }
    }
    .label-item:last-child{
      margin-right: 0;
    }
  }
</style>


